import React from "react";
import { observer } from "mobx-react";
import Typography from "@material-ui/core/Typography";
import StarIcon from "@material-ui/icons/Star";
import ThumbDownIcon from "@material-ui/icons/ThumbDown";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import HomeIcon from "@material-ui/icons/Home";
import VideoIcon from "@material-ui/icons/VideoCall";
import PersonIcon from "@material-ui/icons/Person";
import TabletIcon from "@material-ui/icons/Tablet";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper";
import ButtonGroup from "@material-ui/core/ButtonGroup";

function Help({ store }) {
  function update() {
    document.location.reload();
  }

  function startColor(count) {
    store.setColorCount(count);
    store.setRunningApp("COLOR");
  }

  return (
    <Paper className="help">
      <Typography paragraph />

      <Typography paragraph>Utmaningarna r i fyra kategorier.</Typography>

      <Typography paragraph>
        KON = BOLL KONTROLL. Hr gller det att kunna klara av att hantera
        bollen p ett smidigt stt.
      </Typography>

      <Typography paragraph>
        TEK - TEKNIK. Hr r det mycket kickningar och trix.
      </Typography>

      <Typography paragraph>
        DRI - DRIBBLINGAR. Hr gller det att f till olika finter och driva
        bollen
      </Typography>

      <Typography paragraph>
        FYS - FYSIK. Hr handlar det om att bli starkare, men ven en del
        skottvningar som krver styrka och teknik.
      </Typography>

      <Typography variant="h6" paragraph>
        <HomeIcon /> Hem
      </Typography>

      <div style={{ margin: "10px" }}>
        <Typography paragraph>
          Hr visas fldet med alla ssongens anvndaruppladde filmer.
        </Typography>
        <Typography paragraph>
          <AccountCircleIcon />
          Under utvrdering fr att se om man klarat det.
        </Typography>
        <Typography paragraph>
          <StarIcon />
          Klarad utmaning
        </Typography>
        <Typography paragraph>
          <ThumbDownIcon />
          Utmaning misslyckad. Bara att frska igen. Kanske hade du inte flytet
          i utmaningen, eller missfrttt ngot. Kontrollera videon och
          instruktionerna och frsk igen.
        </Typography>
        <Typography paragraph>
          Om du klickar p personens namn s kan du se profilen.
        </Typography>
        <Typography paragraph>
          Klicka p videon fr att se den i helskrm.
        </Typography>
      </div>

      <Typography variant="h6" paragraph>
        <StarIcon /> Utmaningar
      </Typography>

      <div style={{ margin: "10px" }}>
        <Typography paragraph>
          Hr visas fldet med alla ssongens utmaningar. Det finns 3 olika
          niver. Ltt, medium, svr.
        </Typography>

        <Typography paragraph>
          <VideoIcon />
          Klicka p denna fr att ladda upp en film. Filma alltid i horisontellt
          lge.
        </Typography>
      </div>

      <Typography variant="h6" paragraph>
        <StarIcon /> Topplistan
      </Typography>

      <div style={{ margin: "10px" }}>
        <Typography paragraph>
          Du kan just nu f 90 som mest i din totala raiting.
        </Typography>
      </div>

      <Typography variant="h6" paragraph>
        <PersonIcon /> Profil
      </Typography>

      <div style={{ margin: "10px" }}>
        <Typography paragraph>
          Hr kan du ladda upp din profilbild och andra anvndarinstllningar.
        </Typography>
      </div>

      <Typography variant="h6" paragraph>
        <TabletIcon /> Verktyg
      </Typography>

      {/* <Button variant="outlined" onClick={() => store.setRunningApp("NUMBER")}>
        Nummer
      </Button>

      <Button variant="outlined" onClick={() => startColor(2)}>
        Color 2 frger
      </Button>

      <Button variant="outlined" onClick={() => startColor(4)}>
        Color 4 frger
      </Button> */}

      <Typography paragraph>
        Om det finns nya uppdateringar kan du uppdatera hr.
      </Typography>

      <Button variant="outlined" onClick={update}>
        Uppdatera appen
      </Button>
    </Paper>
  );
}

export default observer(Help);
